.home {
	position: relative;
}
.home__container {
	gap: 3rem;

	@media screen and (min-width: 968px) {
		gap: 1.5rem;
	}
}
.home__data {
	gap: 0.5rem;
	text-align: center;
}
.home__img {
	width: 120px;
	height: 120px;
	border-radius: 50%;
	justify-self: center;
	margin-bottom: var(--mb-1);
}
.home__title {
	font-size: var(--h1-font-size);
}
.home__profession {
	font-size: var(--normal-font-size);
	margin-bottom: var(--mb-1);
}
.home__address {
	gap: 1rem;
}
.home__information {
	display: flex;
	align-items: center;
	font-size: var(--small-font-size);
	color: inherit;
}
.home__icon {
	font-size: 1.2rem;
	margin-right: 0.25rem;
}
.home__button-movil {
	display: inline-block;
	border: 2px solid var(--text-color);
	color: var(--title-color);
	padding: 1rem 2rem;
	border-radius: 0.25rem;
	transition: 0.3s;
	font-weight: var(--font-medium);
	margin-top: var(--mb-3);

	@media screen and (min-width: 968px) {
		display: none;
	}
}
.home__button-movil:hover {
	background-color: var(--text-color);
	color: var(--container-color);
}
