.interests__container {
	grid-template-columns: repeat(3, 1fr);
	margin-top: var(--mb-2);

	@media screen and (min-width: 968px) {
		grid-template-columns: repeat(4, max-content);
		column-gap: 3.5rem;
	}
}
.interests__content {
	display: flex;
	flex-direction: column;
	align-items: center;
}
.interests__icon {
	font-size: 1.5rem;
	color: var(--text-color-light);
	margin-bottom: 0.25rem;
}
